export default {
	name: "Comment",
	//属性
	props: {
		placeholder: {
			type: String
		},
		maskState: {
			type: Number,
			default: 0
		}
	},
	data() {
		return {
			// maskState: 0,
			content: '',
			focus: false
		};
	},
	watch:{
		
	},
	created() {
		
	},
	methods: {
		stopPrevent(){
		},
		toggleMask(type){
			let timer = type === 'show' ? 10 : 300;
			let	state = type === 'show' ? 1 : 0;
			this.$emit('close');
			// this.maskState = 2;
			setTimeout(()=>{
				// this.maskState = state;







				this.focus = this.maskState ? true : false;

			}, timer)
		},
		pubComment() {
			this.$emit('pubComment',this.content);
			this.toggleMask()
		}
	}
}
